// import axios from "../../config/axios-config/axios-instance";
import actions from './actions';
import { rtdb } from "../../library/firebase/firebase";


export const getProducts = (token) => {
    return dispatch => {
        let inventory = rtdb.ref(`catStore/HeadQuarters/inventory/`);
        inventory.on('value', (snapshot) => {
            dispatch(actions.getProducts());
            // console.log(snapshot.val());
            dispatch(actions.itemsSuccess(snapshot.val(), actions.GET_PRODUCTS));
        }, error => {
            if (error) {
                dispatch(actions.itemsFail(error, actions.GET_PRODUCTS));
            }
        });
    }
}


export const getCategories = (token) => {
    return dispatch => {
        let categories = rtdb.ref(`categories/`).orderByChild('time');
        categories.on('value', snapshot => {
            dispatch(actions.getCategories());
            // console.log(snapshot.val());
            let cats = { ...snapshot.val(), Services: { abbreviation: 'Services', name: 'Services' } }
            dispatch(actions.itemsSuccess(cats, actions.GET_CATEGORIES));
        }, error => {
            if (error) {
                dispatch(actions.itemsFail(error, actions.GET_CATEGORIES));
            }
        });
        // axios.get(`categories.json?auth=${token}&orderBy="time"`)
        //     .then(response => {
        //         dispatch(actions.itemsSuccess(response.data, actions.GET_CATEGORIES));
        //     })
        //     .catch(error => {
        //         dispatch(actions.ItemsFail(error, actions.GET_CATEGORIES));
        //     })
    }
}


//CART ACTIONS
export const removeItemsFromCart = (data) => {
    let cartData = {
        [data.id]: {
            category: data.category,
            amount: data.amount,
            delete: data.delete,
        }
    }
    return dispatch => {
        dispatch(actions.removeItemsFromCart(cartData, data.id));
    }
}
export const addItemsToCart = (data) => {
    let cartData = {
        [data.id]: {
            all: data.all,
            category: data.category,
            product: data.name,
            amount: data.amt,
            price: data.cost,
            partNum: data.partNum,
            code: data.code,
            unit: data.unit,
            vendor: data.vendor,
        }
    }
    return dispatch => {
        dispatch(actions.addItemsToCart(cartData, data.id));
    }
}

export const calcItemsToCart = (value, data) => {
    return dispatch => {
        dispatch(actions.calcItemsToCart(value, data));
    }

}

export const cancelCart = () => {
    return dispatch => {
        dispatch(getProducts());
    }
}

export const toggleCheckoutModal = (props) => {

    return dispatch => {
        dispatch(actions.toggleCheckoutModal(props));
    }
}

export const submitCheckoutData = (data, newInventory) => {
    return dispatch => {
        dispatch(actions.submitCheckoutData());
        let checkoutKey = rtdb.ref().child('catOrders/').push().key;
        var updates = {};
        updates['/catOrders/' + checkoutKey] = data;
        updates['catStore/HeadQuarters/inventory'] = newInventory;
        rtdb.ref().update(updates, error => {
            if (error) {
                dispatch(actions.itemsFail(error, actions.SUBMIT_CHECKOUT_DATA));
            } else {
                dispatch(actions.itemsSuccess("checkout", actions.SUBMIT_CHECKOUT_DATA));
            }
        });
    }
}

export const clearMessage = (type) => {
    return dispatch => {
        switch (type) {
            case "error":
                dispatch(actions.clearError());
            case "success":
                dispatch(actions.clearSuccess());
            case "info":
                dispatch(actions.clearInfo());
            default:
                dispatch(actions.clearAllMessages());

        }
    }
}